.moving-background {
  z-index: -1;
  position: absolute;
  background-size: 28%;
  top: 0;
  left: 0;
  background-color: #d6a46a;
  right: 0;
  bottom: 0;
  background-image: url("assets/social-media-icons/bgg.png");
  background-repeat: no-repeat;
  background-position: center center;
  object-fit: cover;
  background-attachment: fixed;
  -moz-background-size: cover;
  /* -webkit-background-size: cover; */
  /* background-size: cover; */
  -o-background-size: cover;
}
.moveDown {
  position: absolute;
  top: 38vh;
  top: 54%;
  left: 50%;
  transform: translate(-50%,-50%)
  
}

.audio {
  position: fixed; 
  right: 0px; 
  bottom: 0px; 
  z-index: 10;
}
.moveRight {
  margin-left: 80px;
}
.moveLeft {
  margin-left: 22vh;
}
